@import "compass";

// Reset layout
@import "compass/reset";

// Typography
@import "partials/typography";

// Layout
@import "partials/layout";

// Theme
@import "theme";


// Links
a {
  @include link-colors($color-link, $color-link);
  @include hover-link;
  font-weight: 400; }

// Body
body {
  background-color: $color-bg; }



